.fm-c-frame {
    position: absolute;
    opacity: 0;
    width: 300px;
    height: 300px;
    top: 50%;
    left: 50%;
    margin-top: -150px;
    margin-left: -150px;
    pointer-events: none;
    z-index: 2;
    border: 2px solid #0b0c0c;
}

.fm-c-frame--visible {
    opacity: 1;
}

.fm-o-viewport--dark-basemap .fm-c-frame {
    border-color: #ffffff;
}